.confirm-dialog {
  padding: 0;
  max-width: 500px;

  .dialog-header {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 24px 24px 16px;
    border-bottom: 1px solid var(--border-color);

    .dialog-icon {
      font-size: 32px;
      width: 32px;
      height: 32px;
      flex-shrink: 0;

      &.icon-info {
        color: var(--primary-color);
      }

      &.icon-warning {
        color: #ff9800;
      }

      &.icon-danger {
        color: #f44336;
      }
    }

    h2 {
      margin: 0;
      font-size: 20px;
      font-weight: 600;
      color: var(--text-primary);
    }
  }

  mat-dialog-content {
    padding: 24px;
    min-height: 60px;

    .dialog-message {
      margin: 0;
      font-size: 14px;
      line-height: 1.6;
      color: var(--text-secondary);
      white-space: pre-line;
    }
  }

  mat-dialog-actions {
    padding: 16px 24px;
    border-top: 1px solid var(--border-color);
    margin: 0;

    .cancel-btn {
      color: var(--text-secondary);
      
      &:hover {
        background: var(--bg-hover);
      }
    }

    .confirm-btn {
      min-width: 80px;
    }
  }

  // 타입별 스타일
  &.dialog-danger {
    .dialog-header {
      background: linear-gradient(to right, rgba(244, 67, 54, 0.05), transparent);
    }
  }

  &.dialog-warning {
    .dialog-header {
      background: linear-gradient(to right, rgba(255, 152, 0, 0.05), transparent);
    }
  }

  &.dialog-info {
    .dialog-header {
      background: linear-gradient(to right, rgba(33, 150, 243, 0.05), transparent);
    }
  }
}

// 다크모드 지원
:host-context(.dark-mode) {
  .confirm-dialog {
    .dialog-header {
      border-bottom-color: rgba(255, 255, 255, 0.1);

      h2 {
        color: #fff;
      }
    }

    mat-dialog-actions {
      border-top-color: rgba(255, 255, 255, 0.1);
    }
  }
}
